import { h, Component } from 'preact';
import style from './contact';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default class Contact extends Component {

	constructor(props) {
		super(props);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.renderErrorsFor = this.renderErrorsFor.bind(this);
		this.renderForm = this.renderForm.bind(this);
		this.validateForm = this.validateForm.bind(this);
		this.state = {
			errors: []
		};
	};

	validateForm() {
		const { email, name, message } = this.state;
		const errors = [];
		if (!name) {
			errors.push({ field: 'name', message: 'Please lets us know who you are.' });
		}
		if (!email || !email.includes('@') ) {
			errors.push({ field: 'email', message: 'Please leave a valid email address so we can get back to you.' });
		} 
		if (!message) {
			errors.push({ field: 'message', message: 'Did you forget to leave a message?' });
		}
		return errors;
	}

	handleSubmit(e) {
		const { name, email, message } = this.state;
		e.preventDefault();
		this.validateForm();
		const errors = this.validateForm();
		if (errors.length > 0) {
			this.setState({ errors });
		} else {
			this.setState({ submitting: true, errors: [] });
			fetch('https://idipifc3ie.execute-api.us-east-1.amazonaws.com/production', {
				method: 'POST',
				mode: 'cors',
				cache: 'no-cache',
				headers: {
					"Content-Type": "application/json; charset=utf-8",
				},
				body: JSON.stringify({ name, email, message })
			}).then(response => {
				this.setState({ submitted: true })
			}).catch(error => {
				this.setState({
					submitting: false,
					errors: [
					{ field: 'submit', message: 'Sorry, there was an error submiting. Please shoot us a mail at info@codingscape.com!' }
					]
				});
			});
		};
	};

	handleChange(e) {
		this.setState({ [e.target.name]: e.target.value });
	};

	renderErrorsFor(field) {
		const { errors } = this.state;
		return errors.filter(e => e.field === field).map((e,i) => (
			<div class={style.error} key={i}>{e.message}</div>
		));
	};

	renderForm() {
		const { email, name, message, submitting } = this.state;
		return (<form onSubmit={this.handleSubmit}>
			<div class="formgroup">
				<label for="name">Name</label>
				<input type="text" name="name" id="name" value={name} onChange={this.handleChange} />
				{this.renderErrorsFor('name')}
			</div>
			<div class="formgroup">
				<label for="email">Email</label>
				<input type="text" name="email" id="email" value={email} onChange={this.handleChange} />
				{this.renderErrorsFor('email')}
			</div>
			<div class="formgroup">
				<label for="message">Message</label>
				<textarea name="message" id="message" rows="4" onChange={this.handleChange} value={message} />
				{this.renderErrorsFor('message')}
			</div>
			{this.renderErrorsFor('submit')}
			{ submitting 
				? <span><FontAwesomeIcon icon="spinner" spin={true} /> Please wait..</span> 
				: <button type="submit">Submit</button>}
		</form>);
	};

	renderSuccess() {
		return (<div class={style.contactSuccess}>Thanks! We'll be in touch soon.</div>)
	}

	render() {
		const { submitted } = this.state;
		
		return (
			<div class={style.contact}>
        <div class={'page-header ' + style.header}></div>

				<div class="jumbo">Do you have a project for us? Let's talk.</div>

				{submitted ? this.renderSuccess() : this.renderForm()}

			</div>
		);
	}
}



